#header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
}

.container {
  background-color: cadetblue;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.header_left {
  background-color: whitesmoke;
}
.header_center {
  background-color: whitesmoke;
}

.header_right {
  background-color: whitesmoke;
}
